// components/MobileSearchButton.tsx
'use client';
import useSearch from '@/hooks/useSearch';
import React from 'react';

export default function MobileSearchButton() {
  const { isMobileSearchVisible, setIsMobileSearchVisible, setSearchTerm } = useSearch();

  const toggleMobileSearch = () => {
    setIsMobileSearchVisible(!isMobileSearchVisible);
    if (!isMobileSearchVisible) setSearchTerm('');
  };

  return (
    <button 
      onClick={toggleMobileSearch}
      className="text-white hover:text-nexus-primary md:hidden" 
      aria-label="Buscar"
    >
      <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" /></svg>
    </button>
  );
}